.filter {
    background-color: rgba(255,255,255,0.2);
    /* width: 100%;
    height: 100%; */
    padding: 25px 25px;
    color: #fff;
    font: 300 22px "Roboto";
}
.filter--title {
    display: block;
    margin: 10px 0;
    font: 700 26px "Roboto";
}
.inputCheckbox {
    display: none;
}
.labelCheckbox {
    cursor: pointer;
    position: relative;
    display: block;
    margin: 5px 0 5px 30px;
}
.labelCheckbox::before {
    content: "";
    position: absolute;
    width: 10px;   
    height: 10px;
    border: 1px solid #fff;
    left: -25px;
    top: 7px;
}
.inputCheckbox:checked + .labelCheckbox::before {
    background: #ffffff;
    transition: ease-in-out 0.2s;
}
.btn-submit {
    width: 100%;
    height: 50px;
    text-transform: uppercase;
    text-align: center;
    line-height: 45px;
    cursor: pointer;
    position: relative;
    background: #235993be;
    border: none;
    color: #ffffff;
    font: 300 21px "Roboto";
    margin-top: 10px;
}
svg {
    position: absolute;
    top: 0;
    left: 0;
}
svg rect {
    stroke: rgb(255, 255, 255);
    stroke-width: 2;
    transition: 0.8s;
    stroke-dasharray: 400, 0;
}
.btn-submit:hover svg rect {
    stroke-width: 5;
    stroke-dasharray: 90, 400;
    stroke-dashoffset: 120;
}